<template>
<body id="pos">
  <div id="app">
    <vue-particles
      id="particles-js"
      color="#dedede"
      :particleOpacity="0.7"
      :particlesNumber="80"
      shapeType="circle"
      :particleSize="4"
      linesColor="#dedede"
      :linesWidth="1"
      :lineLinked="true"
      :lineOpacity="0.4"
      :linesDistance="150"
      :moveSpeed="3"
      :hoverEffect="true"
      hoverMode="grab"
      :clickEffect="true"
      clickMode="push"
    ></vue-particles>
  </div>

  <router-view />
</body>
</template>

<script>
export default {
  name: "App",
  data() {
    return {
      // activeIndex: "1"
    };
  }
};
</script>

<style>
#particles-js {
  width: 100%;
  height: calc(100% - 100px);
  position: absolute;
}

body {
  margin: 0px;
  padding: 0;
}
#app {
  font-family: "Avenir", Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin-top: 0px;
}
#pos {
  background-image: url("./assets/8.jpg");
  background-position: center;
  background-repeat: no-repeat;
  background-size: 100% 100%;
  height: 100%;
  width: 100%;
  background-size: cover;
  position: fixed;
  margin-top: 0px;
}
</style>



