<template>
<body>
  <transition name="el-zoom-in-top">
    <div v-show="show">
      <el-form
        class="register-container"
        label-position="left"
        label-width="0px"
        :rules="rules"
        :model="registerForm"
      >
        <h3 class="register_title">Sign up today, it's free.</h3>
        <el-form-item prop="username">
          <el-input
            type="text"
            v-model="registerForm.username"
            auto-complete="off"
            placeholder="Username"
          ></el-input>
        </el-form-item>

        <el-form-item prop="password">
          <el-input
            type="password"
            v-model="registerForm.password"
            auto-complete="off"
            placeholder="Password"
          ></el-input>
        </el-form-item>

        <el-form-item prop="comfirmpassword">
          <el-input
            type="password"
            v-model="registerForm.comfirmpassword"
            auto-complete="off"
            placeholder="Comfirm Password"
          ></el-input>
        </el-form-item>
        <el-form-item prop="validate">
          <el-row>
            <el-input
              type="text"
              v-model="registerForm.validate"
              placeholder="Validcode"
              style="width: 200px"
            ></el-input>
            <div class="code" @click="refreshCode()">
              <s-identify :identifyCode="identifyCode"></s-identify>
            </div>
          </el-row>
        </el-form-item>
        <!-- <el-form-item style="width: 100%">
        <el-button type="primary" style="width: 100%;background: #505458;border: none" v-on:click="login">Login</el-button>
        </el-form-item>-->
        <el-button
          :type="primary"
          round
          style="font-size:18px;line-height:20px;width: 100%;color: #FFFFFF; background:#505458 ;border: none; "
          @click="register()"
        >Sign up now</el-button>
        <h5></h5>
        <div class="line"></div>
        <el-link
          :underline="false"
          type="info"
          icon="el-icon-user"
          style="font-size:18px;line-height:30px; float:right;"
          @click="gotoLogin()"
        >Already have a account? Log in here!</el-link>
      </el-form>
    </div>
  </transition>
</body>
</template>


<script>
import SIdentify from "@/components/sIdentify.vue";
export default {
  components: { SIdentify },
  name: "register",
  show: false,
  data() {
    return {
      identifyCodes: "1234567890",
      identifyCode: "",
      errorInfo: false,
      registerForm: {
        username: "",
        password: "",
        comfirmpassword: "",
        validate: ""
      },
      rules: {
        username: [
          {
            required: true,
            message: "Username Required",
            trigger: ["blur", "change"]
          }
        ],
        password: [
          {
            required: true,
            message: "Password Required",
            trigger: ["blur", "change"]
          }
        ],
        comfirmpassword: [
          {
            required: true,
            message: "Retype Your Password",
            trigger: ["blur", "change"]
          }
        ],
        validate: [
          {
            required: true,
            message: "Validcode Required",
            trigger: ["blur", "change"]
          }
        ]
      },
      responseResult: []
    };
  },
  // rules: {
  //   username: [{ required: true, message: "请输入登录名", trigger: "blur" }],
  //   password: [{ required: true, message: "请输入密码", trigger: "blur" }],
  //   validate: [{ required: true, message: "请输入验证码", trigger: "blur" }]
  // },
  mounted() {
    this.identifyCode = "";
    this.makeCode(this.identifyCodes, 4);
    this.show = true;
  },
  methods: {
    gotoLogin() {
      this.$router.push("/login");
    },
    register() {
      if (this.registerForm.password != this.registerForm.comfirmpassword) {
        this.$message({
          message: "两次密码不一致哦",
          type: "error"
        });
        this.registerForm.comfirmpassword = "";
      } else if (this.registerForm.validate != this.identifyCode) {
        this.$message({
          message: "验证码错误哦",
          type: "error"
        });
        this.refreshCode();
        this.registerForm.validate = "";
      } else {
        this.$axios
          .post("/register", {
            username: this.registerForm.username,
            password: this.registerForm.password
          })
          .then(res => {
            if (res.data === 0) {
              this.$message({
                message: "用户名已存在哦",
                type: "error"
              });
            } else if (res.data === 1) {
              this.$message({
                message: "注册成功，请前往登录",
                type: "success"
              });
              this.gotoLogin();

              // this.refreshCode();
              // this.loginForm.validate = "";
              // console.log(res.dat
            }
          })
          .catch(failResponse => {});
      }

      // const self = this;
      // self.$refs[formName].validate(valid => {
      //   if (valid) {
      //     if (self.loginForm.validate != this.identifyCode) {
      //       self.errorInfo = true;
      //       self.errInfo = "验证码错误";
      //       self.$message.error("验证码错误");
      //       this.refreshCode();
      //     } else {
      //       this.$axios
      //         .post("/login", {
      //           username: this.loginForm.username, //cdy
      //           password: this.loginForm.password //1234
      //         })
      //         .then(res => {
      //           if (res.data === 1) {
      //             // console.log(res.dat
      //             this.$message({
      //               message: "登录成功，正在跳转。。。。",
      //               type: "success"
      //             });
      //           }
      //         })
      //         .catch(failResponse => {});
      //     }
      //   }
      // });

      //   if (
      //     this.loginForm.username == "cdy" &&
      //     this.loginForm.password == "123"
      //   ) {
      //     if (this.loginForm.validate == this.identifyCode) {
      //       this.$message({
      //         message: "登录成功，正在跳转。。。。",
      //         type: "success"
      //       });
      //     } else {
      //       this.$message({
      //         message: "验证码错误哦",
      //         type: "warning"
      //       });
      //     }
      //     this.refreshCode();
      //     this.loginForm.validate = "";
      //   }
      // this.$axios
      //   .post("/login", {
      //     username: this.loginForm.username, //cdy
      //     password: this.loginForm.password //1234
      //   })
      //   .then(res => {
      //     if (res.data === 1) {
      //       // console.log(res.dat
      //       this.$message({
      //         message: "登录成功，正在跳转。。。。",
      //         type: "success"
      //       });
      //     }
      //   })
      //   .catch(failResponse => {});
    },
    refreshCode() {
      //
      this.identifyCode = "";
      this.makeCode(this.identifyCodes, 4);
    },
    randomNum(min, max) {
      max = max + 1;
      return Math.floor(Math.random() * (max - min) + min);
    },
    // 随机生成验证码字符串
    makeCode(data, len) {
      for (let i = 0; i < len; i++) {
        this.identifyCode += data[this.randomNum(0, data.length - 1)];
      }
    }
  }
};
</script>

<style>
#poster {
  background: url("../assets/8.jpg");
  background-position: center;
  height: 100%;
  width: 100%;
  background-size: cover;
  position: fixed;
}
body {
  margin: 0px;
  padding: 0;
}

.code {
  width: 112px;
  height: 35px;
  border: 1px solid #ccc;
  float: right;
  border-radius: 2px;
}

.register-container {
  border-radius: 15px;
  background-clip: padding-box;
  position: relative;
  margin: 90px auto;
  width: 350px;
  height: 430px;
  padding: 35px 35px 15px 35px;
  background: #fff;
  border: 1px solid #eaeaea;
  box-shadow: 0 0 25px #cac6c6;
}

.register_title {
  margin: 0px auto 40px auto;
  text-align: center;
  color: #505458;
}

.line {
  float: right;
  width: 100%;
  height: 1px;
  margin-top: -0.5em;
  background: #d4c4c4;
  position: relative;
  text-align: center;
}
</style>


