<template>
<body>
  <transition name="el-zoom-in-top">
    <div v-show="show">
      <el-form
        class="login-container"
        label-position="left"
        label-width="0px"
        :rules="rules"
        :model="loginForm"
      >
        <h3 class="login_title">SIGN IN</h3>
        <el-form-item prop="username">
          <el-input
            type="text"
            v-model="loginForm.username"
            auto-complete="off"
            placeholder="Username"
          ></el-input>
        </el-form-item>

        <el-form-item prop="password">
          <el-input
            type="password"
            v-model="loginForm.password"
            auto-complete="off"
            placeholder="Password"
          ></el-input>
        </el-form-item>
        <el-form-item prop="validate">
          <el-row>
            <el-input
              type="text"
              v-model="loginForm.validate"
              placeholder="Validcode"
              style="width: 200px"
            ></el-input>
            <div class="code" @click="refreshCode()">
              <s-identify :identifyCode="identifyCode"></s-identify>
            </div>
          </el-row>
        </el-form-item>
        <!-- <el-form-item style="width: 100%">
        <el-button type="primary" style="width: 100%;background: #505458;border: none" v-on:click="login">Login</el-button>
        </el-form-item>-->
        <el-button
          :type="primary"
          round
          style="font-size:18px;line-height:20px;width: 100%;color: #FFFFFF; background:#505458 ;border: none; "
          @click="login()"
        >Login</el-button>
        <h5></h5>
        <div class="line"></div>
        <el-form-item>
          <el-row>
            <el-link
              :underline="false"
              type="info"
              icon="el-icon-user"
              style="font-size:18px;line-height:30px; float:right;"
              @click="gotoRegister()"
            >Don't have a account? Create one!</el-link>
          </el-row>
        </el-form-item>
      </el-form>
    </div>
  </transition>
</body>
</template>

<script>
import SIdentify from "@/components/sIdentify.vue";
// import { createSocket } from "@/utils/websocket.js";
// import { sendWSPush } from "@/utils/websocket.js";
export default {
  components: { SIdentify },
  name: "login",
  show: false,
  data() {
    return {
      identifyCodes: "1234567890",
      identifyCode: "",
      errorInfo: false,
      loginForm: {
        username: "",
        password: "",
        validate: ""
      },
      rules: {
        username: [
          {
            required: true,
            message: "Username Required",
            trigger: ["blur", "change"]
          }
        ],
        password: [
          {
            required: true,
            message: "Password Required",
            trigger: ["blur", "change"]
          }
        ],
        validate: [
          {
            required: true,
            message: "Validcode Required",
            trigger: ["blur", "change"]
          }
        ]
      },
      responseResult: []
    };
  },

  mounted() {
    this.identifyCode = "";
    this.makeCode(this.identifyCodes, 4);
    this.show = true;
  },
  methods: {
    gotoRegister() {
      this.$router.push("/register");
    },
    login() {
      // this.$message({
      //   message: "登录成功，正在跳转。。。。",
      //   type: "success"
      // });
      // this.$router.push("/pickdate")
      // createSocket("wss://localhost:8080/login");
      // sendWSPush("hello websocket");
      // this.websocket_com.sendMsg('test');
      // console.log(this.websocket_com.data);
      // const self = this;
      // self.$refs[formName].validate(valid => {
      //   if (valid) {
      //     if (self.loginForm.validate != this.identifyCode) {
      //       self.errorInfo = true;
      //       self.errInfo = "验证码错误";
      //       self.$message.error("验证码错误");
      //       this.refreshCode();
      //     } else {
      //       this.$axios
      //         .post("/login", {
      //           username: this.loginForm.username, //cdy
      //           password: this.loginForm.password //1234
      //         })
      //         .then(res => {
      //           if (res.data === 1) {
      //             // console.log(res.dat
      //             this.$message({
      //               message: "登录成功，正在跳转。。。。",
      //               type: "success"
      //             });
      //           }
      //         })
      //         .catch(failResponse => {});
      //     }
      //   }
      // });
      // if (
      //   this.loginForm.username == "cdy" &&
      //   this.loginForm.password == "123"
      // ) {
      //   if (this.loginForm.validate == this.identifyCode) {
      //     this.$message({
      //       message: "登录成功，正在跳转。。。。",
      //       type: "success"
      //     });
      //   } else {
      //     this.$message({
      //       message: "验证码错误哦",
      //       type: "warning"
      //     });
      //   }
      //   this.refreshCode();
      //   this.loginForm.validate = "";
      // }
      if (this.loginForm.validate != this.identifyCode) {
        this.$message({
          message: "验证码错误哦",
          type: "error"
        });
        this.refreshCode();
        this.loginForm.validate = "";
      } else {
        this.$axios
          .post("/login", {
            username: this.loginForm.username, //cdy
            password: this.loginForm.password //1234 // function(string s1, string s2)  处理 ---》 返回
          })
          .then(res => {
            if (res.data === 0) {
              //do something
              this.$message({
                message: "用户名不存在，请前往注册",
                type: "error"
              });
            } else if (res.data === 2) {
              this.$message({
                message: "密码错误哦",
                type: "error"
              });
              this.loginForm.password = "";
            } else if (res.data === 1) {
              this.$message({
                message: "登录成功，正在跳转中。。。。",
                type: "success"
              });
              this.$router.push("/pickdate");
            } else if (res.data === 3) {
              this.$message({
                message: "以管理员身份登录成功，正在跳转中。。。。",
                type: "success"
              });
              this.$router.push("/admin");
            }
          })
          .catch(failResponse => {});
      }
    },
    refreshCode() {
      //
      this.identifyCode = "";
      this.makeCode(this.identifyCodes, 4);
    },
    randomNum(min, max) {
      max = max + 1;
      return Math.floor(Math.random() * (max - min) + min);
    },
    // 随机生成验证码字符串
    makeCode(data, len) {
      for (let i = 0; i < len; i++) {
        this.identifyCode += data[this.randomNum(0, data.length - 1)];
      }
    }
  }
};
</script>


<style>
#poster {
  background: url("../assets/8.jpg");
  background-position: center;
  height: 100%;
  width: 100%;
  background-size: cover;
  position: fixed;
}
body {
  margin: 0px;
  padding: 0;
}

.code {
  width: 112px;
  height: 35px;
  border: 1px solid #ccc;
  float: right;
  border-radius: 2px;
}

.login-container {
  border-radius: 15px;
  background-clip: padding-box;
  position: relative;
  margin: 90px auto;
  width: 350px;
  height: 350px;
  padding: 35px 35px 15px 35px;
  background: #fff;
  border: 1px solid #eaeaea;
  box-shadow: 0 0 25px #cac6c6;
}

.login_title {
  margin: 0px auto 40px auto;
  text-align: center;
  color: #505458;
}

.line {
  float: right;
  width: 100%;
  height: 1px;
  margin-top: -0.5em;
  background: #d4c4c4;
  position: relative;
  text-align: center;
}
</style>



