<template>
<body>
  <el-row>
    <h1 class="myfont">Free Weather Searching Tool For Everyone</h1>
    <h2
      style="font-size: 40px; text-align: center; line-height: 1.7;"
    >Clean and Powerful, Easy-to-use</h2>
    <h3 style="font-size: 35px; text-align: center; line-height: 1.7;">Start Here ↓</h3>
    <el-button
      type="success"
      style="font-size:20px;line-height:20px; margin-left:550px;"
      @click="goosignin()"
    >SIGN IN</el-button>
    <div class="verticalBar"></div>
    <el-button
      type="danger"
      style="font-size:20px;line-height:20px; margin-left:0px;"
      @click="goosignup()"
    >SIGN UP</el-button>
  </el-row>
</body>
</template>

<script>
export default {
  name: "Welcome",
  data() {
    return {
      navList: [
        { name: "/", navItem: "HOME" },
        { name: "/login", navItem: "SIGN IN" },
        { name: "/register", navItem: "SIGN UP" }
      ]
    };
  },
  methods: {
    goosignin() {
      this.$router.push("/login");
    },
    goosignup() {
      this.$router.push("/register");
    },
    handleSelect(key, keyPath) {}
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.verticalBar {
  width: 7px;
  height: 50px;
  background: red;
  display: inline-block;
  margin-top: 0px;
  vertical-align: top;
  margin-right: 29px;
  margin-left: 30px;
}

.loo {
  left: 300px;
}
.myfont {
  font-size: 50px;
  font-family: "等线 Light";
  line-height: 1.7;
  text-align: center;
}
body {
  margin: 0px;
  padding: 0;
}

ul {
  list-style-type: none;
  padding: 0;
  text-align: center;
}
li {
  display: inline-block;
  margin: 0px;
}
a {
  color: #42b983;
}
</style>



